import React, {useState} from 'react'
import ReactDOM from 'react-dom'
import {w3cwebsocket as W3CWebSocket} from 'websocket'
import {
	makeStyles,
	Container,
	Paper,
	Grid,
	CircularProgress,
} from '@material-ui/core'
import {Timer, MyButton, Login, Chat, RenderBoard, GetReady} from './components'
import './static/index.css'
import csshake from 'csshake/dist/csshake.min.css'
import {DEV_ENV} from './config'
import {
	handleUserNameInput,
	onSubmit,
	handleUserInput,
	handleChatMessage,
	resetGame,
	endGame,
	launchAttack,
	deleteValue,
	sendMessage,
	handleAttacks,
	getDankMemes
} from './handlers'

export const client = new W3CWebSocket('ws://192.168.100.211:8080')
const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
	button: {
		margin: theme.spacing(1),
	},
	buttonProgress: {
		color: 'green',
		position: 'absolute',
		marginTop: 15,
		marginLeft: -70,
	},
}))
if (!DEV_ENV) {
	console.log = () => {
		/*i dont do anything*/
	}
	console.warn = () => {
		/*i dont do anything*/
	}
	console.error = () => {
		/*i dont do anything*/
	}
}
const Game = () => {
	const [isLoggedIn, setIsLoggedIn] = useState(false) //toggle logiin
	const [userName, setUserName] = useState()
	const [userActivity, setUserActivity] = useState([])
	const [fieldInput, setFieldInput] = useState('')
	const [opponentFields, setOpponentFields] = useState({})
	const [playerNumber, setPlayerNumber] = useState('')
	const [tempName, setTempName] = useState('')
	const [allPlayers, setAllPlayers] = useState(0)
	const classes = useStyles()
	const [messageHistory, setMessageHistory] = useState([])
	const [time, setTime] = useState(0)
	const [board, setBoard] = useState([])
	const [cooldown, setCooldown] = useState(true)
	let dataFromServer
	const [shake, setShake] = useState({board: null, cell: null})
	const [memes, setMemes] = useState(false)
	const [ready, setReady] = useState(false)
	////// Websocket functions start///////////////////
	client.onopen = () => {
		sendMessage(null, null, 'ready') //tell srv we're ready
		console.log('WebSocket Client Connected to server')
	}
	client.onclose = () => {
		console.warn('WebSocket server closing or offline...')
	}
	client.onmessage = (message) => {
		// console.log(message)
		dataFromServer = JSON.parse(message.data)
		console.log('im RECIEVING parsed:', dataFromServer)
		// console.log('im player ', playerNumber)

		if (dataFromServer.type === 'info') {
			if (dataFromServer.players) {
				setAllPlayers(dataFromServer.players)
			}
			if (dataFromServer.board) {
				setBoard(dataFromServer.board)
			}
			return
		}
		if (dataFromServer.type === 'time') {
			setTime(dataFromServer.time)
			return
		}
		if (dataFromServer.type === 'readyup') {
			setReady(true)
			setCooldown(false)
			return
		}
		if (dataFromServer.type === 'userevent') {
			/* ON USEREVENT*/
			console.log(
				`${tempName}local ... server'${dataFromServer.data.username}`,
			)
			if (tempName === dataFromServer.data.username) {
				let index = dataFromServer.data.userActivity.length - 1
			console.log(
				`UserActivity index: ${dataFromServer.data.userActivity[index]}`,
			)
			let newestActivity = [
				...userActivity,
				dataFromServer.data.userActivity[index],
			]
			setUserActivity(newestActivity)
				setUserName(dataFromServer.data.username)
				setIsLoggedIn(true)
			} else if (dataFromServer.data.username === 'UsrNameTaken') {
				alert('username already exists! sorry :c ')
				setIsLoggedIn(false)
			}
			if (dataFromServer.data.playersReady > 0) {
				setAllPlayers(dataFromServer.data.playersReady)
			}
		}

		if (dataFromServer.type === 'resetgame') {
			let index = dataFromServer.data.userActivity.length - 1
			let newestActivity = [
				...userActivity,
				dataFromServer.data.userActivity[index],
			]
			setUserActivity(newestActivity)
			setFieldInput(undefined)
			// console.log(fieldInput)
		}

		if (dataFromServer.type === 'chat') {
			setMessageHistory([...dataFromServer.data.chat])
			// console.log('index history ', messageHistory)
		}

		if (dataFromServer.type === 'gamemove') {
			console.log('in gamemove')

			if (playerNumber === 'spectator') {
				if (dataFromServer.data.gameField1) {
					setFieldInput(dataFromServer.data.gameField1)
					setOpponentFields(dataFromServer.data.gameField2)
					console.log(fieldInput)
				}
			} else {
				console.log(
					`my number ${playerNumber} message num ${dataFromServer.data.player}`,
				)
				if (dataFromServer.data.player === playerNumber) {
					console.log('I made a move')
					setFieldInput(dataFromServer.data.gameField)
					console.log(fieldInput)
				} else {
					console.log('OPPONENT made a move')
					setOpponentFields(dataFromServer.data.gameField)
				}
			}
		}
		if (dataFromServer.type === 'endgame') {
			setMessageHistory([
				dataFromServer.data.userActivity[
					dataFromServer.data.userActivity.length - 1
				],
			])
			return
		}
		if (dataFromServer.type === 'attack') {
			let index = dataFromServer.data.userActivity.length - 1
			let newestActivity = [
				...userActivity,
				dataFromServer.data.userActivity[index],
			]
			setUserActivity(newestActivity)
			let attack = handleAttacks(dataFromServer, playerNumber)
			if (attack === 'onCooldown') {
				setCooldown(true)
				setTimeout(function() {
					setCooldown(false)
				}, 15000)
				return
			}
			if (attack && attack.state ? attack.state : null) {
				if (
					(attack.target === playerNumber) |
					(playerNumber === 'spectator')
				) {
					switch (attack.type) {
						case 'SHAKE':
							setShake({
								board:
									'shake-slow shake-constant shake-constant-hover',
								cell: 'shake-freeze shake-crazy',
							})
							setTimeout(function() {
								setShake({board: null, cell: null})
							}, 30000)
							break

						case 'BLACK':
							setShake({board: 'black', cell: 'black'})
							setTimeout(function() {
								setShake({board: null, cell: null})
							}, 30000)
							break
						case 'MEME':
						console.log('inside memes gen')
						getDankMemes(setMemes)
						setTimeout(function() {
								setMemes(false)
							}, 30000)
						break
						case 'SWITCH':
						break
					}
				}
			}
		}
	}
	////// Websocket functions end///////////////////
	return (
		<div className="game">
			<Container>
				<div className="userActivity">
					activity: {userActivity[userActivity.length - 1]}
				</div>
			</Container>
			{!isLoggedIn ? (
				<Login
					onSubmit={() =>
						onSubmit(
							playerNumber,
							tempName,
							allPlayers,
							setPlayerNumber,
						)
					}
					handleUserInput={(e) =>
						handleUserNameInput(e, setTempName, tempName)
					}
					handlePlayerSelect={(e) => setPlayerNumber(e)}
					usernameSet={tempName ? false : true}
				/>
			) : (
				<div>
					{(!ready && !isNaN(playerNumber) )? <GetReady playerName={userName} setReady={setReady} playerNumber={playerNumber}/> : console.log(ready)}
					<Container>
						<Timer time={time} />
					</Container>
					{memes ? <div className="memecontainer"><div className="memes">{memes}</div></div> : ''} 
					<Container className="fieldContainer">
						<Grid container>
							<Grid
								item
								xs={6}
								className={'gameField1 ' + shake.board}
							>
								<Paper className={classes.paper}>
									<RenderBoard
										deleteValue={(cell) =>
											deleteValue(
												cell,
												userName,
												playerNumber,
											)
										}
										fields={board}
										handleUserInput={(e, cellID) =>
											handleUserInput(
												e,
												cellID,
												userName,
												playerNumber,
											)
										}
										inputValues={fieldInput}
										opponentFields={opponentFields}
										player={playerNumber}
										shake={shake.cell}
									/>
									{(isNaN(playerNumber) ) ? (
										<div>
											<MyButton
												onClick={() =>
													launchAttack(
														userName,
														playerNumber,
													)
												}
												text="Attack"
												color="secondary"
												cooldown={cooldown}
											/>
											,
											 {(cooldown | !ready )? (
												<CircularProgress
													size={24}
													className={
														classes.buttonProgress
													}
												/>
											) : (
												''
											)}
										</div>
									) : (
										<Grid container>
											<Grid item xs={6}>
												<MyButton
													onClick={() =>
														resetGame(
															userName,
															playerNumber,
														)
													}
													text="reset"
													color="default"
												/>
											</Grid>
											<Grid item xs={6}>
												<MyButton
													onClick={() =>
														endGame(
															userName,
															playerNumber,
															fieldInput,
														)
													}
													text="i'm done!"
													color="primary"
												/>
												<br />
											</Grid>
										</Grid>
									)}
									
								</Paper>
							</Grid>
							<Grid item xs={6} className="playField2">
								<Paper className={classes.paper}>
									<RenderBoard
										fields={board}
										handleUserInput
										opponent="true"
										opponentFields={opponentFields}
										player={playerNumber}
									/>
								</Paper>
							</Grid>
						</Grid>
					</Container>
					<Container>
						<Chat
							userName={userName}
							client={client}
							history={messageHistory}
							onMessage={(msg) =>
								handleChatMessage(msg, userName, playerNumber)
							}
						/>
					</Container>
				</div>
			)}
		</div>
	)
}
ReactDOM.render(<Game />, document.getElementById('root'))