//Timer
import React from 'react'
import MyButton from './button'
import {sendMessage} from '.././handlers'
export const GetReady = (props) => {
	const imReady = (setReady,e) => {
		sendMessage(null,props.playerNumber, 'readyup','Player is ready')
	}
	return (
		<div className="getReady">
			<div className="readyBtn"><MyButton color="primary" onClick={()=>{imReady(props.setReady)}} text="Ready Up!"/></div>
		</div>
	)
}
export default GetReady